/******************************************************/
/* pc category-nav
/******************************************************/
#category-nav{
  width: 260px;
  height: 100%;
  position: fixed;
  left:-260px;
  top:0;
  background: #333;
  box-sizing: border-box;
  color: #fff;
  transition: all .5s ease;
  z-index: 20;
  a{
      color: #fff;
      text-decoration: none;
  }

  a:visited{
      color: #fff;
      text-decoration: none;
  }

  a:hover{
      color: #ccc;
      text-decoration: none;
  }

  a:active{
      color: #fff;
      text-decoration: none;
  }
  .scroller{
    padding:60px 30px;
  }
  .category-list{
    margin-bottom: 30px;
  }
  .category_ttl{
    font-size: 18px;
    margin-bottom: 5px;
  }
  ul#menu-commodity{
      & > li{
        a span.menu_count{
          background: #fff;
          color: #333;
          border-radius: 10px;
          padding: 0 5px;
          font-size: 12px!important;
          margin-left: 5px;
        }
        line-height: 2em;
        margin-bottom: 10px;
        font-size: 13px;
        .sub-menu{
          padding-left: 0.5em;
          li{
            line-height: 1.8;
            font-size: 11px;
          }
        }
      }
  }

  ul#menu-color{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    li{
        margin: 8px;
    }
    li a{
      display: block;
      width: 24px;
      height: 24px;
      background: #fff;
      text-indent: -9999px;
      border-radius: 12px;
      position: relative;
      &:before{
        content: "";
        display: block;
        position: absolute;
        top: -31px;
        left: -50%;
        width: 50px;
        height: 22px;
        color: #333;
        font-size: 10px;
        text-indent:0;
        text-align: center;
        background: #fff;
        border-radius: 4px;
        // border: 1px solid #ccc;
        opacity: 0;
        visibility: hidden;
      }
      &:after{
        content: "";
        display: block;
        position: absolute;
        top: -10px;
        left: 6px;
        width: 0;
        height: 0;
        border-top:6px solid #fff;
        border-left:6px solid transparent;
        border-right:6px solid transparent;
        border-bottom:6px solid transparent;
        opacity: 0;
      }
      &:hover:before , &:hover:after{
        opacity: 1;
        visibility: visible;
      }
    }
    li.c1 a:before{ content: "ホワイト";}
    li.c2 a:before{ content: "ブラック";}
    li.c3 a:before{ content: "グレー";}
    li.c4 a:before{ content: "ブラウン";}
    li.c5 a:before{ content: "ベージュ";}
    li.c6 a:before{ content: "グリーン";}
    li.c7 a:before{ content: "ブルー";}
    li.c8 a:before{ content: "パープル";}
    li.c9 a:before{ content: "イエロー";}
    li.c10 a:before{ content: "ピンク";}
    li.c11 a:before{ content: "レッド";}
    li.c12 a:before{ content: "オレンジ";}
    li.c13 a:before{ content: "シルバー";}
    li.c14 a:before{ content: "ゴールド";}
    li.c15 a:before{ content: "その他";}
    li.c1 a{ background: #fff;}
    li.c2 a{ background: #000;}
    li.c3 a{ background: #9e9e9e;}
    li.c4 a{ background: #733422;}
    li.c5 a{ background: #e5d6ae;}
    li.c6 a{ background: #32984d;}
    li.c7 a{ background: #116ae1;}
    li.c8 a{ background: #8600c6;}
    li.c9 a{ background: #edd400;}
    li.c10 a{ background: #ff71bd;}
    li.c11 a{ background: #d70a2e;}
    li.c12 a{ background: #fd9239;}
    li.c13 a{
      background: #f9f9f9; /* Old browsers */
      background: -moz-linear-gradient(-45deg,  #f9f9f9 0%, #a8a8a8 100%); /* FF3.6-15 */
      background: -webkit-linear-gradient(-45deg,  #f9f9f9 0%,#a8a8a8 100%); /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(135deg,  #f9f9f9 0%,#a8a8a8 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f9f9f9', endColorstr='#a8a8a8',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    }
    li.c14 a{
      background: #e5d5ae; /* Old browsers */
      background: -moz-linear-gradient(-45deg,  #e5d5ae 0%, #c79810 100%); /* FF3.6-15 */
      background: -webkit-linear-gradient(-45deg,  #e5d5ae 0%,#c79810 100%); /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(135deg,  #e5d5ae 0%,#c79810 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#e5d5ae', endColorstr='#c79810',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

    }
    li.c15 a{
      background: #ff0004; /* Old browsers */
      background: -moz-linear-gradient(-45deg,  #ff0004 15%, #2e00ff 38%, #0cff00 61%, #ffe316 85%); /* FF3.6-15 */
      background: -webkit-linear-gradient(-45deg,  #ff0004 15%,#2e00ff 38%,#0cff00 61%,#ffe316 85%); /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(135deg,  #ff0004 15%,#2e00ff 38%,#0cff00 61%,#ffe316 85%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ff0004', endColorstr='#ffe316',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
    }
  }
}
#overray{
  position: fixed;
  width: 100%;
  height: 100%;
  left:0;
  top: 0;
  background: rgba(0,0,0,0.5);
  z-index: 10;
  display: none;
}

#contents-slide-wrap.active #category-nav{
  left:0px;
}


/******************************************************/
/* pagetop
/******************************************************/

.pagetop{
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: -40px;
  right: 20px;
  z-index: 20;
  border-radius: 20px;
  background: #444;
  cursor: pointer;
  transition: all 0.3s ease;
  &:after{
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    margin-top: 5px;
    border-top: 2px solid #fff;
    border-right: 2px solid #fff;
    transform: rotate(-45deg);
  }
  &:hover{
    background: #666;
  }
}

.pagetop.active{
  bottom: 20px;
}
